.projects-page {
    padding: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects-page h1 {
    font-size: 2rem;
    margin-bottom: 20px;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px; /* Spacing between project boxes */
    justify-content: center; /* Center-aligns the project boxes */
}

.project-box {
    width: 300px; /* Adjust width as needed */
    height: 400px; /* Adjust height as needed */
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    position: relative;
}

.project-box:hover {
    transform: translateY(-10px); /* Pops out effect */
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3);
}

.project-link {
    text-decoration: none;
    color: inherit;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.project-title {
    padding: 16px;
    font-size: 1.5rem;
    font-weight: bold;
    background-color: #f7f7f7;
    border-bottom: 1px solid #ddd;
}

.project-content {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fafafa;
}

.project-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
}

/* Remove footer-related styles */
.github-link {
    display: none; /* Hide the GitHub link if it was part of the footer */
}

.github-icon {
    display: none; /* Hide the GitHub icon if it was part of the footer */
}